import React, { useEffect, useState } from 'react';
import axios from '../Axios'; // Custom Axios instance
import { useParams, useNavigate } from 'react-router-dom';
import './EditProduct.css';

function EditProduct() {
  const [product, setProduct] = useState({ name: '', price: 0, quantity: 0, gstRate: 0 });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const { id } = useParams();
  const navigate = useNavigate();

  useEffect(() => {
    const fetchProduct = async () => {
      try {
        const res = await axios.get(`/products/${id}`);
        setProduct({
          name: res.data.name || '',
          price: res.data.price || 0,
          quantity: res.data.quantity || 0,
          gstRate: res.data.gstRate ?? 0,
        });
      } catch (err) {
        console.error(' Error fetching product:', err);
        setError(' Failed to fetch product.');
      } finally {
        setLoading(false);
      }
    };
    fetchProduct();
  }, [id]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setProduct(prev => ({
      ...prev,
      [name]: name === 'price' || name === 'quantity' || name === 'gstRate' ? Number(value) : value
    }));
  };

  const handleIncrement = (field) => {
    setProduct(prev => ({
      ...prev,
      [field]: Number(prev[field]) + 1
    }));
  };

  const handleDecrement = (field) => {
    setProduct(prev => ({
      ...prev,
      [field]: Number(prev[field]) > 0 ? Number(prev[field]) - 1 : 0
    }));
  };

  const handleUpdate = async (e) => {
    e.preventDefault();
    try {
      await axios.put(`/products/${id}`, product);
      alert(' Product updated successfully!');
      navigate('/product-list'); // Adjust route if needed
    } catch (err) {
      console.error(' Update error:', err);
      setError(' Update failed. Please try again.');
    }
  };

  if (loading) return <p className="loading"> Loading product...</p>;
  if (error) return <p className="error">{error}</p>;

  return (
    <div className="edit-container">
      <h2>Edit Product</h2>
      <form onSubmit={handleUpdate} className="edit-form">
        <label>
          Product Name:
          <input
            name="name"
            value={product.name}
            onChange={handleChange}
            placeholder="Enter product name"
            required
          />
        </label>

        <label>
          Price:
          <div className="input-group">
            <button type="button" onClick={() => handleDecrement('price')}></button>
            <input
              name="price"
              type="number"
              min="0"
              step="0.01"
              value={product.price}
              onChange={handleChange}
              required
            />
            <button type="button" onClick={() => handleIncrement('price')}>+</button>
          </div>
        </label>

        <label>
          Quantity:
          <div className="input-group">
            <button type="button" onClick={() => handleDecrement('quantity')}></button>
            <input
              name="quantity"
              type="number"
              min="0"
              value={product.quantity}
              onChange={handleChange}
              required
            />
            <button type="button" onClick={() => handleIncrement('quantity')}>+</button>
          </div>
        </label>

        <label className="gst-label">
          <span className="gst-heading">GST Rate:</span>
          <select
            name="gstRate"
            value={product.gstRate}
            onChange={handleChange}
            required
            className="gst-select"
          >
            <option value={0}>0% (Exempted/Essential)</option>
            <option value={5}>5% (Food, Books, etc.)</option>
            <option value={12}>12% (Processed Food, Computers, etc.)</option>
            <option value={18}>18% (Electronics, Services, etc.)</option>
            <option value={28}>28% (Luxury, Cars, etc.)</option>
          </select>
        </label>

        <button type="submit" className="update-btn"> Update</button>
      </form>
    </div>
  );
}

export default EditProduct;
