import React, { useEffect, useState } from 'react';
import axios from '../Axios';
import { useNavigate } from 'react-router-dom';
// import './ProductList.css'; // Remove custom CSS for Tailwind

const ProductList = () => {
  const [products, setProducts] = useState([]);
  const navigate = useNavigate();

  // Fetch products from API
  useEffect(() => {
    fetchProducts();
  }, []);

  const fetchProducts = async () => {
    try {
      const res = await axios.get('/products/all');
      setProducts(res.data);
    } catch (err) {
      console.error(" Error fetching products", err);
    }
  };

  // Handle Edit
  const handleEdit = (product) => {
    navigate(`/edit-product/${product._id}`);
  };

  // Handle Delete
  const handleDelete = async (id) => {
    if (!window.confirm("Are you sure you want to delete this product?")) return;

    try {
      await axios.delete(`/products/${id}`);
      alert(" Product deleted successfully");
      fetchProducts(); // Refresh after delete
    } catch (err) {
      alert(" Failed to delete product");
    }
  };

  return (
    <div className="max-w-4xl w-full mx-auto px-2 sm:px-4 py-4 bg-gradient-to-r from-blue-50 to-blue-200 rounded-2xl shadow-md font-sans">
      
      {/*  Top Bar: Heading + Add Button */}
      <div className="flex flex-col sm:flex-row items-center justify-between mb-4 gap-2">
        <h2 className="text-2xl font-bold text-gray-800"> Product List</h2>
        <button
          className="bg-green-500 hover:bg-green-600 text-white font-semibold py-2 px-4 rounded transition"
          onClick={() => navigate('/add-product')}
        >
           Add Product
        </button>
      </div>

      {/*  Product Table */}
      <div className="overflow-x-auto">
        <table className="min-w-full text-sm border bg-white rounded-lg">
          <thead>
            <tr className="bg-gray-100">
              <th className="p-2 border">S.No.</th>
              <th className="p-2 border">Name</th>
              <th className="p-2 border">Price</th>
              <th className="p-2 border">Stock</th>
              <th className="p-2 border">Actions</th>
            </tr>
          </thead>
          <tbody>
            {products.length === 0 ? (
              <tr>
                <td colSpan="5" className="text-center py-6 text-gray-400">
                  No products available.
                </td>
              </tr>
            ) : (
              products.map((prod, index) => (
                <tr key={prod._id}>
                  <td className="p-2 border">{index + 1}</td>
                  <td className="p-2 border">{prod.name}</td>
                  <td className="p-2 border">{prod.price}</td>
                  <td className="p-2 border">{prod.quantity}</td>
                  <td className="p-2 border">
                    <div className="flex gap-2">
                      <button
                        className="bg-blue-500 hover:bg-blue-600 text-white py-1 px-3 rounded text-xs font-semibold transition"
                        onClick={() => handleEdit(prod)}
                      >
                         Edit
                      </button>
                      <button
                        className="bg-red-500 hover:bg-red-600 text-white py-1 px-3 rounded text-xs font-semibold transition"
                        onClick={() => handleDelete(prod._id)}
                      >
                         Delete
                      </button>
                    </div>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default ProductList;
