import React, { useEffect, useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import axios from '../Axios';
import './InvoiceView.css';

const InvoiceView = () => {
  const { id } = useParams(); // get :id from route
  const [invoice, setInvoice] = useState(null);

  useEffect(() => {
    const fetchInvoice = async () => {
      try {
        const res = await axios.get(`/invoices/${id}`);
        setInvoice(res.data);
      } catch (err) {
        console.error("Error fetching invoice:", err);
      }
    };

    fetchInvoice();
  }, [id]);

  if (!invoice) return <p className="loading-text">Loading invoice...</p>;

  return (
    <div className="invoice-container">
      <Link
        to="/dashboard"
        style={{
          display: 'inline-block',
          marginBottom: '16px',
          padding: '8px 16px',
          borderRadius: '6px',
          background: '#6c757d',
          color: '#fff',
          border: 'none',
          fontWeight: 600,
          textDecoration: 'none',
          cursor: 'pointer'
        }}
        className="no-print"
      >
         Back to Dashboard
      </Link>
      <h2> Invoice: {invoice.invoiceNumber}</h2>
      <p><strong>Date:</strong> {new Date(invoice.createdAt).toLocaleString()}</p>
      <p><strong>Customer:</strong> {invoice.customer?.name || 'Anonymous'}</p>
      <p><strong>Phone:</strong> {invoice.customer?.phone || 'N/A'}</p>

      <table className="invoice-table">
        <thead>
          <tr>
            <th>Item</th>
            <th>Qty</th>
            <th>Price</th>
            <th>Total</th>
          </tr>
        </thead>
        <tbody>
          {invoice.items.map((item, i) => (
            <tr key={i}>
              <td>{item.name}</td>
              <td>{item.quantity}</td>
              <td>{item.price}</td>
              <td>{item.total}</td>
            </tr>
          ))}
        </tbody>
      </table>

      <div className="totals">
        <p><strong>GST (18%):</strong> {invoice.gst}</p>
        <p><strong>Grand Total:</strong> {invoice.grandTotal}</p>
      </div>

      {/* Print Invoice Button */}
      <button
        className="print-invoice-btn"
        onClick={() => setTimeout(() => window.print(), 300)}
        style={{ marginTop: '18px', padding: '10px 18px', borderRadius: '6px', background: '#007bff', color: '#fff', border: 'none', fontWeight: 600, cursor: 'pointer' }}
      >
         Print Invoice
      </button>
    </div>
  );
};

export default InvoiceView;
