import React, { useState } from 'react';
import axios from '../Axios';
import './InvoiceSearch.css';

const InvoiceSearch = () => {
  const [filters, setFilters] = useState({
    from: '',
    to: '',
    customer: '',
    invoice: '',
  });

  const [results, setResults] = useState([]);

  const handleChange = (e) => {
    setFilters({ ...filters, [e.target.name]: e.target.value });
  };

  const handleSearch = async (e) => {
    e.preventDefault();
    const params = new URLSearchParams();
    Object.entries(filters).forEach(([key, val]) => {
      if (val.trim() !== '') params.append(key, val);
    });

    try {
      const res = await axios.get(`/invoices/search?${params.toString()}`);
      setResults(res.data);
    } catch (err) {
      console.error('Search failed:', err);
    }
  };

  return (
    <div className="invoice-search-container">
      <h2> Invoice Search</h2>

      {/*  Filters */}
      <form className="filters" onSubmit={handleSearch}>
        <input type="date" name="from" value={filters.from} onChange={handleChange} />
        <input type="date" name="to" value={filters.to} onChange={handleChange} />
        <input type="text" name="customer" placeholder="Customer Name" value={filters.customer} onChange={handleChange} />
        <input type="text" name="invoice" placeholder="Invoice Number" value={filters.invoice} onChange={handleChange} />
        <button type="submit">Search</button>
      </form>

      {/*  Results Table */}
      <table className="invoice-table">
        <thead>
          <tr>
            <th>Invoice #</th>
            <th>Customer</th>
            <th>Amount</th>
            <th>Date</th>
          </tr>
        </thead>
        <tbody>
          {results.length > 0 ? (
            results.map((inv) => (
              <tr key={inv._id}>
                <td>{inv.invoiceNumber || 'N/A'}</td>
                <td>{inv.customer?.name || 'N/A'}</td>
                <td>{inv.grandTotal?.toFixed(2) || '0.00'}</td>
                <td>{new Date(inv.createdAt).toLocaleDateString()}</td>
              </tr>
            ))
          ) : (
            <tr>
              <td colSpan="4" style={{ textAlign: 'center', padding: '10px' }}>
                No invoices found.
              </td>
            </tr>
          )}
        </tbody>
      </table>
    </div>
  );
};

export default InvoiceSearch;
