import React, { useEffect, useState } from 'react';
import axios from '../Axios';
// import './InvoiceHistory.css';

const InvoiceHistory = () => {
  const [invoices, setInvoices] = useState([]);
  const [filteredInvoices, setFilteredInvoices] = useState([]);
  const [query, setQuery] = useState('');
  const [loading, setLoading] = useState(false);
  const [expandedInvoiceId, setExpandedInvoiceId] = useState(null);

  useEffect(() => {
    const fetchInvoices = async () => {
      try {
        setLoading(true);
        const res = await axios.get('/invoices/all');
        setInvoices(res.data);
        setFilteredInvoices(res.data);
      } catch (err) {
        alert(' Failed to load invoices');
      } finally {
        setLoading(false);
      }
    };
    fetchInvoices();
  }, []);

  useEffect(() => {
    const term = query.toLowerCase();
    const filtered = invoices.filter(inv =>
      inv.invoiceNumber?.toLowerCase().includes(term) ||
      inv.customer?.name?.toLowerCase().includes(term)
    );
    setFilteredInvoices(filtered);
  }, [query, invoices]);

  const toggleExpand = (id) => {
    setExpandedInvoiceId(prev => (prev === id ? null : id));
  };

  return (
    <div className="max-w-5xl w-full mx-auto px-2 sm:px-4 py-4 bg-gradient-to-r from-blue-50 to-blue-200 rounded-2xl shadow-md font-sans">
      <h2 className="text-2xl font-bold text-gray-800 mb-4 text-center"> Invoice History</h2>

      <div className="flex justify-center mb-4">
        <input
          type="text"
          className="border rounded px-3 py-2 text-sm w-full max-w-xs focus:outline-none focus:ring-2 focus:ring-blue-400"
          placeholder="Search by Name/Invoice"
          value={query}
          onChange={(e) => setQuery(e.target.value)}
        />
      </div>

      {loading ? (
        <p className="text-center text-gray-500">Loading invoices...</p>
      ) : filteredInvoices.length === 0 ? (
        <p className="text-center text-gray-400">No invoices found.</p>
      ) : (
        <div className="overflow-x-auto">
          <table className="min-w-full text-xs border bg-white rounded-lg">
            <thead>
              <tr className="bg-gray-100">
                <th className="p-2 border">#</th>
                <th className="p-2 border">Invoice No</th>
                <th className="p-2 border">Customer</th>
                <th className="p-2 border">Products</th>
                <th className="p-2 border">Amount</th>
                <th className="p-2 border">GST</th>
                <th className="p-2 border">Date</th>
                <th className="p-2 border">Time</th>
              </tr>
            </thead>
            <tbody>
              {filteredInvoices.map((inv, index) => (
                <React.Fragment key={inv._id || index}>
                  <tr>
                    <td className="p-2 border">{index + 1}</td>
                    <td className="p-2 border">{inv.invoiceNumber || 'N/A'}</td>
                    <td className="p-2 border">{inv.customer?.name || 'N/A'}</td>
                    <td className="p-2 border">
                      <button
                        className="bg-blue-500 hover:bg-blue-600 text-white py-1 px-3 rounded text-xs font-semibold transition"
                        onClick={() => toggleExpand(inv._id)}
                      >
                        {expandedInvoiceId === inv._id ? 'Hide' : 'View'}
                      </button>
                      <a
                        href={`/invoice/${inv._id}`}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="ml-2 text-purple-600 hover:underline text-lg"
                        title="Print Invoice"
                      >
                        
                      </a>
                    </td>
                    <td className="p-2 border">{inv.grandTotal?.toFixed(2) || '0.00'}</td>
                    <td className="p-2 border">{inv.gst?.toFixed(2) || '0.00'}</td>
                    <td className="p-2 border">{new Date(inv.createdAt).toLocaleDateString()}</td>
                    <td className="p-2 border">{new Date(inv.createdAt).toLocaleTimeString()}</td>
                  </tr>
                  {expandedInvoiceId === inv._id && (
                    <tr>
                      <td colSpan="8" className="bg-gray-50">
                        <table className="min-w-full text-xs border bg-white rounded-lg my-2">
                          <thead>
                            <tr className="bg-gray-200">
                              <th className="p-2 border">Product</th>
                              <th className="p-2 border">Qty</th>
                              <th className="p-2 border">Price</th>
                              <th className="p-2 border">Total</th>
                            </tr>
                          </thead>
                          <tbody>
                            {(inv.items || []).map((item, idx) => (
                              <tr key={idx}>
                                <td className="p-2 border">{item.name || item.product?.name || item.productName || 'Unnamed'}</td>
                                <td className="p-2 border">{item.quantity || 0}</td>
                                <td className="p-2 border">{item.price?.toFixed(2) || '0.00'}</td>
                                <td className="p-2 border">{(item.quantity * item.price)?.toFixed(2) || '0.00'}</td>
                              </tr>
                            ))}
                          </tbody>
                        </table>
                      </td>
                    </tr>
                  )}
                </React.Fragment>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
};

export default InvoiceHistory;
