import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import axios from '../Axios';
// import './Dashboard.css';

function DashboardPage() {
  const [data, setData] = useState(null);
  const [query, setQuery] = useState('');
  const [allProducts, setAllProducts] = useState([]);
  const [range, setRange] = useState('today');
  const [from, setFrom] = useState('');
  const [to, setTo] = useState('');

  useEffect(() => {
    const fetchDashboard = async () => {
      try {
        let url = `/dashboard/overview?range=${range}`;
        if (range === 'custom') {
          if (from && to) url += `&from=${from}&to=${to}`;
          else return;
        }
        const res = await axios.get(url);
        setData(res.data);
      } catch (err) {
        console.error("Dashboard load error:", err);
      }
    };
    fetchDashboard();
    const interval = setInterval(fetchDashboard, 30000);
    return () => clearInterval(interval);
  }, [range, from, to]);

  useEffect(() => {
    axios.get('/products/all')
      .then(res => setAllProducts(res.data))
      .catch(err => console.error("Error fetching all products:", err));
  }, []);

  const filteredProducts = allProducts.filter(p =>
    p.name.toLowerCase().includes(query.toLowerCase())
  );

  if (!data) return <p className="text-center py-8 text-gray-500 text-lg">Loading dashboard...</p>;

  return (
    <div className="max-w-3xl w-full mx-auto px-2 sm:px-4 py-4 bg-gradient-to-r from-blue-50 to-blue-200 rounded-2xl shadow-md font-sans">
      <h2 className="text-2xl sm:text-3xl font-bold mb-4 text-center text-gray-800">Dashboard</h2>

      {/* Filter by Date */}
      <div className="flex flex-col sm:flex-row items-center gap-2 mb-4 flex-wrap">
        <label htmlFor="range-select" className="text-sm font-medium text-gray-700">Select Date Range:</label>
        <select
          id="range-select"
          value={range}
          onChange={(e) => setRange(e.target.value)}
          className="border rounded px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400 bg-white"
        >
          <option value="today">Today</option>
          <option value="week">This Week</option>
          <option value="month">This Month</option>
          <option value="custom">Custom Range</option>
        </select>
        {range === 'custom' && (
          <div className="flex gap-2">
            <input type="date" value={from} onChange={(e) => setFrom(e.target.value)} className="border rounded px-2 py-1 text-sm" />
            <input type="date" value={to} onChange={(e) => setTo(e.target.value)} className="border rounded px-2 py-1 text-sm" />
          </div>
        )}
      </div>

      {/* Summary Cards */}
      <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-6 gap-3 mb-6">
        {[
          { label: 'Total Products', value: data.totalProducts ?? 0 },
          { label: 'Total Invoices', value: data.totalInvoices ?? 0 },
          { label: 'Total Revenue', value: `${data.totalRevenue?.toFixed(2) ?? '0.00'}` },
          { label: "Today's Sales", value: `${data.todaysSales?.toFixed(2) ?? '0.00'}` },
          { label: 'Total Customers', value: data.totalCustomers ?? 0 },
          { label: 'Avg Invoice Value', value: `${data.avgInvoiceValue?.toFixed(2) ?? '0.00'}` }
        ].map((item, idx) => (
          <div className="bg-white rounded-xl shadow p-3 flex flex-col items-center border border-gray-100 hover:shadow-lg transition" key={idx}>
            <div className="text-xs text-gray-500 mb-1 text-center">{item.label}</div>
            <div className="text-lg font-bold text-blue-700 text-center">{item.value}</div>
          </div>
        ))}
      </div>

      {/* Low Stock */}
      <div className="bg-yellow-50 border border-yellow-300 rounded-lg p-3 mb-6">
        <div className="font-semibold mb-2 text-yellow-800"> Low Stock Products (Qty &lt; 5)</div>
        {data.lowStockProducts?.length > 0 ? (
          <ul className="pl-5 space-y-1 list-disc">
            {data.lowStockProducts.map(p => (
              <li key={p._id} className={p.quantity === 0 ? 'text-red-600 font-bold bg-red-50 px-2 py-1 rounded' : 'text-yellow-700'}>{p.name}  <span>{p.quantity}</span></li>
            ))}
          </ul>
        ) : (
          <div className="text-green-700"> All stocks are sufficient.</div>
        )}
      </div>

      {/* Recent Invoices */}
      <div className="font-semibold mb-2 text-gray-700"> Recent Invoices</div>
      {data.recentInvoices?.length > 0 ? (
        <ul className="divide-y divide-gray-200 mb-4">
          {data.recentInvoices.map(inv => (
            <li key={inv._id} className="flex flex-col sm:flex-row sm:items-center justify-between py-2 text-sm">
              <span>#{inv.invoiceNumber || 'N/A'}  {inv.grandTotal?.toFixed(2) ?? '0.00'}  {inv.customer?.name || 'N/A'}</span>
              <Link to={`/invoice/${inv._id}`} className="text-blue-600 hover:underline mt-1 sm:mt-0"> View</Link>
            </li>
          ))}
        </ul>
      ) : (
        <div className="text-gray-500 mb-4">No recent invoices found.</div>
      )}

      <hr className="my-4 border-gray-300" />
      <Link to="/invoice-history" className="block w-full sm:w-auto text-center bg-blue-600 text-white rounded py-2 px-4 font-semibold mb-6 hover:bg-blue-700 transition"> View All Invoices</Link>

      {/* Product Stock Table */}
      <div className="font-semibold mb-2 text-gray-700"> Product Stock Table</div>
      <input
        type="text"
        placeholder="Search Products..."
        className="border rounded px-3 py-2 text-sm mb-2 w-full max-w-xs focus:outline-none focus:ring-2 focus:ring-blue-400"
        value={query}
        onChange={(e) => setQuery(e.target.value)}
      />
      <div className="overflow-x-auto">
        <table className="min-w-full text-xs border bg-white rounded-lg">
          <thead>
            <tr className="bg-gray-100">
              <th className="p-2 border">Name</th>
              <th className="p-2 border">Price</th>
              <th className="p-2 border">Quantity</th>
            </tr>
          </thead>
          <tbody>
            {filteredProducts.length > 0 ? (
              filteredProducts.map(prod => (
                <tr key={prod._id}>
                  <td className="p-2 border">{prod.name}</td>
                  <td className="p-2 border">{prod.price}</td>
                  <td className={`p-2 border ${prod.quantity === 0 ? 'text-red-600 font-bold bg-red-50' : prod.quantity < 5 ? 'text-yellow-700' : 'text-green-700'}`}>{prod.quantity}</td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan="3" className="text-center py-4 text-gray-400">
                  No products available.
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default DashboardPage;
